import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'
//  @  是代表根目录的意思  src
// import Home from '../pages/home/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
    path: '/',
    name: 'Home',
    component: Home
   },
    {
      path: '/city',
      name: 'city',
      component: City
    },
    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detail
    }

  ],
  //  多个页面的拖动会互相影响，因此每次做路由切换的时候，始终回到最顶部
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



