<template>
  <div>
    <!--to="."   后退到当前目录-->
     <router-link
    tag="div" class="header-abs" to="/"
     v-show="showAbs"
    :style="opacityStyle2"
     >
        <span class="iconfont header-abs-back">&#xe624;</span>
      </router-link>
    <!--  固定定位 -->
      <div class="header-fixed"
           v-show="!showAbs"
           :style="opacityStyle"
      >
        <router-link to="/">
          <div class="iconfont header-fixed-back">&#xe624;</div>
        </router-link>
        景点详情
      </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      },
      opacityStyle2: {
        opacity: 1
      }
    }
  },
  methods: {
    handleScroll () {
      // var m={key:'',};
      // console.log(m)

      let top = document.documentElement.scrollTop
      // console.log(top);
      //   QQ浏览器有坑，必须使用这个
      var topPos = document.body.scrollTop;
      if(topPos!=0){
        top=topPos;
      }
      // if(top>10){
      //   //  scrollTop  获取不到，在手机端，你应该查看一下
      //   //  QQ浏览器的这个东西是如何获取的
      //   alert(top)
      // }

      //   滚动的距离坐标大于60，就显示导航条
      if (top > 60) {
        //  渐隐渐现效果..为什么是140.。因为是两个元素的宽高合起来，当地一个元素完全看不到的时候
        //第二个元素的透明度才完全为1。。。好吧，其实140是随便写的
        let opacity = top / 140
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = { opacity }
        this.showAbs = false
      } else {
        let opacity = top / 30
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle2 = { opacity:1-opacity };
        if(opacity>0.6){
          this.opacityStyle2 = { opacity:1-opacity+0.1 };
        }

        this.showAbs = true
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  // //   这里我们注释掉，因为我们用的是第二种解决方案
  //   <keep-alive exclude="Detail">
  // activated () {
  //   window.addEventListener('scroll', this.handleScroll)
  // },
  // //解绑事件，因为这是一个全局的绑定，对所有组件的滚动都会生效
  // //  这是值得注意的地方
  // deactivated(){
  //   window.removeEventListener('scroll', this.handleScroll)
  // },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header-abs
    position: absolute
    left: .2rem
    top: .2rem
    width: .8rem
    height: .8rem
    line-height: .8rem
    border-radius: .4rem
    text-align: center
    background: rgba(0, 0, 0, .8)
    .header-abs-back
      color: #fff
      font-size: .4rem
  .header-fixed
    z-index: 200
    position: fixed
    top: 0
    left: 0
    right: 0
    height: $headerHeight
    line-height: $headerHeight
    text-align: center
    color: #fff
    background: $bgColor
    font-size: .32rem
    .header-fixed-back
      position: absolute
      top: 0
      left: 0
      width: .64rem
      text-align: center
      font-size: .4rem
      color: #fff
</style>



