<template>
  <div class="header">
    城市选择
    <router-link to="/">
      <div class="iconfont header-back">&#xe624;</div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'CityHeader'
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    box-sizing:border-box
    /* 子绝父相  */
    position:relative
    overflow :hidden
    height:.86rem
    line-height :.86rem
    background :#00bcd4
    text-align :center
    color: #fff
    font-size :.32rem
    .header-back
      position:absolute
      width :.64rem
      top: 0
      left: 0
      text-align: center
      font-size: .4rem
      color: #fff
  /*
  .header
    position: relative
    overflow: hidden
    height: $headerHeight
    line-height: $headerHeight
    text-align: center
    color: #fff
    background: $bgColor
    font-size: .32rem
    .header-back
      position: absolute
      top: 0
      left: 0
      width: .64rem
      text-align: center
      font-size: .4rem
      color: #fff
     */
</style>



