import mutations from "./mutations";

export default {
  changeCity_a (ctx, city) {
    //  ctx是上下文。action是 通过调用mutations的 changeCity来使用该方法
    //  ctx=this.$store实例。。组件中使用mutation..this.$store.commit('changeCity',city);
    ctx.commit('changeCity',city);
  }
}



