<template>
  <div>
    <city-header></city-header>
    <city-search :cities="cities"></city-search>
    <city-list :hot="hotCities" :cities="cities" :city="this.$store.state.city" :letter="letter"></city-list>
    <city-alphabet :cities="cities" @clickAlphabet="handleLetterChange"></city-alphabet>
  </div>
</template>

<script>
import axios from 'axios'
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'
export default {
  name: 'City',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      city:'',
      letter: {key:'A',index:'0'}
    }
  },
  methods: {
    getCityInfo () {
      // axios.get('/api/city.json')
      axios.get('./api/city.json')
        .then(this.handleGetCityInfoSucc)
        .catch(function (error) {
          console.log(error);
        });
    },
    handleGetCityInfoSucc (res) {
      console.log(res)
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        //  字母表城市
        this.cities = data.cities;
        this.city = res.city;
        //  热门城市
        this.hotCities = data.hotCities
      }
    },
    // 这是用来改变索引的,,h
    handleLetterChange (letter) {
      // console.log('发生字母点击事件：')
      // console.log(letter)
      this.letter = letter
    }
  },
  mounted () {
    //  请求后端数据
    this.getCityInfo()
  }
}
</script>

<style lang="stylus" scoped>

</style>



